import Api from './api'

export default {
    error: {
        code: '',
        message: '',
    },
    // 載入管端帳號列表
    async getBillsResult(params) {
        let resp = null;
        // 搜尋帳號
        let o = {
            page: params.page,
            type: params.type,
            account: params.account,
            vendor: params.vendor,
            ball_type: params.ball_type,
            game_type: params.game_type,
            play_type: params.play_type,
            bg_game: params.bg_game,
            lt_game: params.lt_game,
            ry_game: params.ry_game,
        }

        switch (params.type) {
            case 'cur': // 當日
                if (params.count_date) o.count_date = params.count_date
                break;
            case 'his': // 歷史
                if (params.count_date) o.count_date = params.count_date
                if (params.count_date_end) o.count_date_end = params.count_date_end
                break;
            case 'time': // 時間
                if (params.time_start) o.time_start = params.time_start
                if (params.time_end) o.time_end = params.time_end
                break;
            case 'post': // 未匯帳
                if (params.entry_time) o.entry_time = params.entry_time
                if (params.entry_time_end) o.entry_time_end = params.entry_time_end
                break;
        }

        const query = Api.queryBuild(o);
        let url = `/report/getBillsResult?${query}`

        let searchData = ''
        if (params.search_tree) searchData = { params: { search_tree: params.search_tree } }


        await axios.get(url, searchData).then(res => {
            resp = res.response || res;
        }).catch(error => {
            console.log(error.response)
        });

        return Api.getResponse(resp, this.error.message)
    },
}