<script>
import { mapGetters, mapActions } from 'vuex'
import { common } from '@/utils'
import _report from '@/utils/api/report'

export default {
  name: "ReportBillResult",
  data() {
    return {
      isMobile: false,
      // 懶加載
      lazy: {
        // 最後更新時間戳
        time: 0,
        loading: false,
        vendor: [],
        play_type: '',
        page: 1
      },
      // 載入資料列表
      tableList: {
        // 無資料顯示
        showNoData: '沒有匹配結果',
        // 表格欄位
        headers: [ // 各 show 值為是否顯示該列資料，由於顯示 checkbox 預設全選，所以顯示為 true
          { text: '比賽種類', value: 'type', show: false, align: 'left', sortable: false },
          { text: '#', value: 'id', show: true, align: 'left', sortable: true },
          { text: '下注時間/開賽時間', value: 'betTime', show: true, align: 'center', sortable: false },
          { text: '館別', value: 'vendor', show: true, align: 'center', sortable: true },
          { text: '項目', value: 'ball', show: true, align: 'center', sortable: true }, // 體彩是用 ball
          // { text: '項目', value: 'game', show: true, align: 'center', sortable: true }, // 賓果、彩球、皇家是用 game
          { text: '資訊', value: 'info', show: true, align: 'center', sortable: true },
          { text: '玩法', value: 'type', show: true, align: 'center', sortable: true },
          { text: '投注內容', value: 'betContent', show: true, align: 'center', sortable: true },
          { text: '投注金額', value: 'cmount', show: true, align: 'center', sortable: false },
          { text: '有效投注', value: 'bmount', show: true, align: 'center', sortable: true },
          { // index: 10
            text: '會員',
            stratumId: 1, // 顯示 checkbox 對應 id
            show: true,
            align: 'center',
            sortable: false,
            children: [
              { text: '返水', value: 'g0_comm', show: true, align: 'center', sortable: true },
              { text: '結果', value: 'g0_mount', show: true, align: 'center', sortable: true }
            ]
          },
          { // index: 11
            text: '代理商',
            stratumId: 2,
            show: true,
            align: 'center',
            sortable: false,
            children: [
              { text: '上級返水', text2: '未拆帳', value: 'g1_comm', show: true, align: 'center', sortable: true, open: false },
              { text: '結果', value: 'g1_mount', show: true, align: 'center', sortable: true }
            ]
          },
          { // index: 12
            text: '總代理',
            stratumId: 3,
            show: true,
            align: 'center',
            sortable: false,
            children: [
              { text: '上級返水', text2: '未拆帳', value: 'g2_comm', show: true, align: 'center', sortable: true, open: false },
              { text: '結果', value: 'g2_mount', show: true, align: 'center', sortable: true }
            ]
          },
          { // index: 13
            text: '股東',
            stratumId: 4,
            show: true,
            align: 'center',
            sortable: false,
            children: [
              { text: '上級返水', text2: '未拆帳', value: 'g3_comm', show: true, align: 'center', sortable: true, open: false },
              { text: '結果', value: 'g3_mount', show: true, align: 'center', sortable: true }
            ]
          },
          { // index: 14
            text: '大股東',
            stratumId: 5,
            show: true,
            align: 'center',
            sortable: false,
            children: [
              { text: '上級返水', text2: '未拆帳', value: 'g4_comm', show: true, align: 'center', sortable: true, open: false },
              { text: '結果', value: 'g4_mount', show: true, align: 'center', sortable: true }
            ]
          },
          { // index: 15
            text: '總監',
            stratumId: 6,
            show: true,
            align: 'center',
            sortable: false,
            children: [
              { text: '上級返水', text2: '未拆帳', value: 'g5_comm', show: true, align: 'center', sortable: true, open: false },
              { text: '結果', value: 'g5_mount', show: true, align: 'center', sortable: true }
            ]
          },
          { // index: 16
            text: '大總監',
            stratumId: 7,
            show: true,
            align: 'center',
            sortable: false,
            children: [
              { text: '上級返水', text2: '未拆帳', value: 'g6_comm', show: true, align: 'center', sortable: true, open: false },
              { text: '結果', value: 'g6_mount', show: true, align: 'center', sortable: true }
            ]
          }
        ],
        // 資料內容(無顯示分類 Bar 資料)
        contents: [],
        // 資料內容2(顯示分類 Bar 資料)
        contents2: {
          // 單式
          straight: [],
          // 過關
          pass: []
        },
        // 底部合計
        totals: {
          show:false,
          betAmount: 0,
          effectiveBet: 0,
          member: {
            g0_comm: 0,
            g0_mount: 0
          },
          agent: {
            g1_comm: 0,
            g1_mount: 0,
            g1_result: 0
          },
          generalAgent: {
            g2_comm: 0,
            g2_mount: 0,
            g2_result: 0
          },
          shareholder: {
            g3_comm: 0,
            g3_mount: 0,
            g3_result: 0
          },
          majorShareholder: {
            g4_comm: 0,
            g4_mount: 0,
            g4_result: 0
          },
          director: {
            g5_comm: 0,
            g5_mount: 0,
            g5_result: 0
          },
          majorDirector: {
            g6_comm: 0,
            g6_mount: 0,
            g6_result: 0
          }
        }
      },

      /* 館別/項目 */
      // 對照[體彩]館別/項目選項
      mapBallType: {
        1: '美棒',
        2: '日棒',
        3: '台棒',
        4: '韓棒',
        5: '冰球',
        6: '籃球',
        7: '彩球',
        8: '美足',
        9: '網球',
        10: '足球',
        11: '指數',
        12: '賽馬',
        13: '電競',
        14: '其他'
        // 15: '世足'
      },
      // 對照[BingoStar]館別/項目選項
      mapBgType: {
        super_big_small: '超級玩法：大小',
        super_odd_even: '超級玩法：單雙',
        super_guess: '超級玩法：單猜',
        normal_big_small_tie: '一般玩法：大小',
        normal_odd_even_draw: '一般玩法：單雙',
        seasons: '四季',
        elements: '五行',
        other_fanbodan: '猜不出',
        star: '星號'
      },
      // 對照[彩球]館別/項目選項
      mapLtType: {
        11: '六合彩',
        12: '大樂透',
        13: '今彩539',
        22: '天天樂'
      },
      // 對照[皇家]館別/項目選項
      mapRyType: {
        bacc: '百家樂',
        fanTan: '翻攤',
        lunPan: '輪盤',
        shaiZi: '骰寶',
        longHu: '龍虎',
        insuBacc: '保險百家樂'
      },
      /* 類型 */
      // 對照[體彩]類型選項
      mapBallGameType: {
        0: '全場',
        1: '上半場',
        2: '下半場',
        3: '第一節',
        4: '第二節',
        5: '第三節',
        6: '第四節',
        7: '走地全場',
        8: '走地上半',
        9: '走地下半',
        10: '多種玩法'
      },
      /* 玩法 */
      // 對照[體彩]玩法選項
      mapBallPlayType: {
        1: '讓分',
        2: '大小',
        3: '獨贏',
        4: '單雙',
        5: '一輸二贏',
        10: '搶首分',
        11: '搶尾分',
        13: '單節最高分',
        14: '波膽',
        20: '過關'
      },

      // 注單明細開合搜尋條件資料
      membersearchInfo: {
        itemsOfVendor: [],
        gameType: '',
        playType: ''
      },

      // 返回功能資訊
      backInfo: {
        // 返回上層報表要搜尋的帳號(上上層帳號)
        backAccount: '',
        // 返回上層報表要搜尋的帳號層級(上上層帳號層級)
        backLevel: '',
        // 返回上層報表要搜尋的帳號層級線
        searchTree: {}
      },
      // 秒數倒數更新
      reciprocalItems: {
        selected: '',
        options: [
          { text: '不更新', value: '' },
          { text: '每10秒更新', value: 10 },
          { text: '每30秒更新', value: 30 }
        ],
        // 倒數 timer
        updateListTimer: null,
        // 當前秒數
        currentSpeed: '',
        // 確定更新秒數
        confirmSpeed: ''
      },
      // 館別 (pe體彩, lt彩球, ry皇家, bg賓果) @看看能不能寫繼承@
      // gameHallItems: {}
      // 層級顯示
      stratumItems: {
        show:true,
        items: [
          { id: 1, name: '會員' },
          { id: 2, name: '代理商' },
          { id: 3, name: '總代理' },
          { id: 4, name: '股東' },
          { id: 5, name: '大股東' },
          { id: 6, name: '總監' },
          { id: 7, name: '大總監' }
        ],
        selected: [1, 2, 3, 4, 5, 6, 7]
      },
      // 搜尋條件資訊
      searchInfo: {
        // 展開狀態
        show: false,
        // 館別/項目
        itemsOfVendor: '',
        // 類型(體彩才有)
        gameType: '',
        // 玩法(體彩才有)
        playType: '',
        // 帳務日期/投注時間
        showTime: '',
        // 會員帳號
        account: '',
        // 會員暱稱
        nickname: ''
      },
      // 對應館別
      // 說明: key 必須與 options value 一樣，value 必須與 options text 一樣
      mapVendorItems: {
        pe: '體彩',
        bg: 'BingoStar',
        lt: 'Super彩球',
        ry: '皇家'
      },
      // 欲搜尋館別
      searchVendorItems: [],
      // searchItems: {} @看看能不能寫繼承@
      // 頁面載入狀態
      loadingStatus: false
    }
  },
  computed: {
    ...mapGetters({
      getDashboardInfoState: 'account-management/getDashboardInfoState'
    }),
    // 層級全選
    stratumSelectAll: {
      get: function () {
        return this.stratumItems.items ? this.stratumItems.selected.length === this.stratumItems.items.length : false
      },
      set: function (value) {
        var selected = []
        if (value) {
          for (var i in this.stratumItems.items) {
            selected.push(this.stratumItems.items[i].id)
          }
        }
        this.stratumItems.selected = selected
      }
    }
  },
  watch: {
    'stratumItems.selected' (items) {
      // 開關各層級上級退水與未拆帳資訊
      this.switchCommission(items)
    },
    'gameHallItems.selected' (value, before) {
      // console.log('gameHallItems', 'value:', value, 'before:', before , 'searchItems.vendor:', this.searchItems.vendor)
      let getChangeStatut = value !== this.searchItems.vendor

      if (before) {
        this.searchItems.vendor = value

        // 初始懶加載設定
        this.resetLazy({
          vendor : (value).split(","),
        })

        // 初始列表內容
        this.tableList.contents = []
        this.tableList.contents2 = { straight: [], pass: [] }
        // 刷新列表
        this.loadBillsResult2(false, getChangeStatut)
      }
    },
    // 每秒更新切換
    'reciprocalItems.selected' (value) {
      this.updateTableList()
    },
    'reciprocalItems.currentSpeed' (value) {
      // 倒數為0秒時刷新列表
      if (value === 0) {
        // 重置懶加載設定
        this.resetLazy({
          vendor : (this.searchItems.vendor).split(","),
        })

        // 初始列表內容
        this.tableList.contents = []
        this.tableList.contents2 = { straight: [], pass: [] }
        // 刷新列表
        this.loadBillsResult2()
      }
    }
    // #這會影響 vendor 設置
    // getDashboardInfoState () {
    //   this.updateDashboardInfo()
    // }
  },
  mounted () {
    this.isMobile = common.isMobile()
    _report.error.message = "取得會員注單明細失敗"
    this.$nextTick(function() {
      this.watchScroll()
    })

    // 依登入層級顯示層級勾選項目
    if (sessionStorage.getItem('super.manage.level')) this.updateStratum(sessionStorage.getItem('super.manage.level')) // 管理帳號層級
    else if (sessionStorage.getItem('super.manage.subLevel')) this.updateStratum(sessionStorage.getItem('super.manage.subLevel')) // 子帳號層級
    // 開關各層級上級退水與未拆帳資訊
    this.switchCommission(this.stratumItems.selected)
  },
  destroyed () {
    this.watchScroll(false)

    // 清除 timer
    clearInterval(this.reciprocalItems.updateListTimer)
  },
  methods: {
    ...mapActions({
      getBillsResult: 'api/getBillsResult',
      getDownAccount: 'api/getDownAccount'
    }),
    /**
     * 重置lazy
     */
    resetLazy (o) {
      // 判斷若一進來play_type(勾選項目)只有20(過關選項)，則直接帶20
      // 反之，若play_type(勾選項目)有很多選項，其中有20(過關選項)的話，先過濾掉
      let game_play_type = this.$route.query.searchItems.play_type === '20'
      ? this.$route.query.searchItems.play_type
      : (this.$route.query.searchItems.play_type.includes('20')
        ? this.$route.query.searchItems.play_type.split(',').filter(function (value) { return value !== '20' }).join(',')
        : this.$route.query.searchItems.play_type)
      this.lazy.play_type = (o.play_type)? o.play_type : game_play_type
      this.lazy.vendor = (o.vendor)? o.vendor : []
      this.lazy.page = 1
    },
    /**
     * 監聽卷軸高度
     *
     * 1秒內不重覆執行
     * 懶加載館別沒有就不執行
     */
    onScroll () {
      if(this.lazy.vendor.length === 0) {
        this.watchScroll(false)
        return
      }
      if (Number(new Date().getTime()) - Number(this.lazy.time) <= 1000) return
      // 當前滾軸高度
      let sum = common.getScrollTop() + common.getWindowHeight()
      // 手機高度*0.4
      let max = this.isMobile
        ? common.getScrollHeight() * 0.4
        : common.getScrollHeight()
      /**
       * 當前滾軸高度 >= 滾軸最高高度
       */
      if (sum >= max) {
        // 刷新列表
        this.loadBillsResult2(true)
      }
    },
    /**
     * 執行監聽
     */
    watchScroll (bol=true) {
      if (bol === true)
        window.addEventListener("scroll", this.onScroll)
      else
        window.removeEventListener("scroll", this.onScroll) // 離開頁面取消監聽
    },
    /**
     * 取得會員注單明細
     */
    async loadBillsResult2 (lazy = false, getChangeStatut) {
      let done = false
      switch (true) {
        case (this.loadingStatus === true):
        case (this.lazy.loading === true):
        case this.searchItems.account === "": // 無帳號不搜尋
          done = true
          break
      }
      if (done === true) return
      this.watchScroll()
      // 懶加載讀取中
      if (lazy) {
        this.lazy.loading = true
        this.lazy.time = new Date().getTime()
      } else {
        // 初始列表內容
        this.tableList.contents = []
        this.tableList.contents2 = { straight: [], pass: [] }
        this.loadingStatus = true
      }

      let arr = [...this.lazy.vendor]
      // 參數
      let param = {
        page: 1,
        account: this.searchItems.account,
        time_start: (this.searchItems.time_start)? this.searchItems.time_start : '',
        time_end: (this.searchItems.time_end)? this.searchItems.time_end : '',
        entry_time: (this.searchItems.entry_time)? this.searchItems.entry_time : '',
        entry_time_end: (this.searchItems.entry_time_end)? this.searchItems.entry_time_end : '',
        count_date: (this.searchItems.count_date)? this.searchItems.count_date : '',
        count_date_end: (this.searchItems.count_date_end)? this.searchItems.count_date_end : '',
        type: this.searchItems.type,
        vendor: "", // 館別 (pe體彩, lt彩球, ry皇家)
        play_type: "", // 體彩玩法
        search_tree: this.searchItems.search_tree // 層級搜尋樹
      }

      // 體彩項目
      if (this.searchItems.hasOwnProperty('ball_type')) param.ball_type = this.searchItems.ball_type
      // 體彩類型
      if (this.searchItems.hasOwnProperty('game_type')) param.game_type = this.searchItems.game_type
      // 彩球項目
      if (this.searchItems.hasOwnProperty('lt_game')) param.lt_game = this.searchItems.lt_game
      // 皇家項目
      if (this.searchItems.hasOwnProperty('ry_game')) param.ry_game = this.searchItems.ry_game

      while (arr.length > 0) {
        let vendor = arr[0]
        param.vendor = vendor
        param.play_type = this.lazy.play_type

        param.page = this.lazy.page
        // 回傳的list 過關key=peParlay
        let key = (vendor === 'pe' && param.play_type === '20' ) ? 'peParlay' : vendor
        // 取明細
        let res = await _report.getBillsResult(param)
        if (!res) break
        // 設定明細
        await this.setBillsResult(res, getChangeStatut)
        // 資料小於100 重製page 移除館別
        if (res[key].length < 100) {
          // 體彩若有選過關，則第一次先搜非過關，非過關載入完，再搜過關
          if (vendor === "pe" &&
              param.play_type !== "20" &&
              this.$route.query.searchItems.play_type.includes('20')) {
            this.lazy.play_type = "20" // 第二次換抓過關 抓完才移除pe
            this.lazy.page = 1
          } else {
            arr.shift()
            this.lazy.vendor.shift() // 紀錄跳出迴圈後下次起始館別從哪開始
            this.lazy.page = 1
            if(this.lazy.vendor.length===0) this.tableList.totals.show = true
          }
        } else {
          // 資料大於100筆 結束
          this.lazy.page++
          break
        }
      }

      // 懶加載讀取中
      if (lazy) {
        this.lazy.loading = false
      } else {
        this.loadingStatus = false
      }
    },
    /**
     * 整理注單明細
     */
    async setBillsResult (data, getChangeStatut) {
      return new Promise((resolve, reject) => {
        // 預設total 隱藏
        this.tableList.totals.show = false
        // 重組資料
        this.sortData(data, this.tableList.contents, this.tableList.contents2)

        this.membersearchInfo.itemsOfVendor = data.itemsOfVendor
        this.membersearchInfo.gameType = data.gameType
        this.membersearchInfo.playType = data.playType

        // 更新表格上方搜尋資訊
        // this.searchInfo.itemsOfVendor = data.itemsOfVendor
        // this.searchInfo.gameType = data.gameType
        // this.searchInfo.playType = data.playType
        this.searchInfo.showTime = data.showTime

        this.setMembersearchInfo(getChangeStatut)

        // 各合計資料
        this.tableList.totals.betAmount = this.countTotal(
          this.tableList.contents,
          "cmount"
        )
        this.tableList.totals.effectiveBet = this.countTotal(
          this.tableList.contents,
          "bmount"
        )
        let key_list = [
          "member",
          "agent",
          "generalAgent",
          "shareholder",
          "majorShareholder",
          "director",
          "majorDirector"
        ]
        for (let k in key_list) {
          let key = key_list[k]
          for (let i in this.tableList["totals"][key]) {
            this.tableList.totals[key][i] = this.countTotal(
              this.tableList.contents,
              i
            )
          }
        }

        resolve()
      })
    },
    /**
     * 整理資料into tablelist
     */
    sortData (res, items, items2) {
      // 重組資料
      if (res.hasOwnProperty("pe")) {
        // 體彩一般單
        for (let i in res.pe) {
          res.pe[i].vendor = "pe"
          items.push(res.pe[i])
          items2.straight.push(res.pe[i])
        }
      }
      if (res.hasOwnProperty("bg")) {
        // 賓果單
        for (let i in res.bg) {
          res.bg[i].vendor = "bg"
          items.push(res.bg[i])
          items2.straight.push(res.bg[i])
        }
      }
      if (res.hasOwnProperty("lt")) {
        // Super彩球單
        for (let i in res.lt) {
          res.lt[i].vendor = "lt"
          items.push(res.lt[i])
          items2.straight.push(res.lt[i])
        }
      }
      if (res.hasOwnProperty("ry")) {
        // 皇家單
        for (let i in res.ry) {
          res.ry[i].vendor = "ry"
          items.push(res.ry[i])
          items2.straight.push(res.ry[i])
        }
      }
      if (res.hasOwnProperty("peParlay")) {
        // 體彩過關單
        for (let i in res.peParlay) {
          res.peParlay[i].vendor = "pe"
          items.push(res.peParlay[i])
          items2.pass.push(res.peParlay[i])
        }
      }

      let id = 0
