    composeSearchInfo (searchItems, vendor) {
      // 初始空值
      this.searchInfo.itemsOfVendor = []
      this.searchInfo.gameType = ''
      this.searchInfo.playType = ''
      if (vendor.includes('pe')) {
        this.searchInfo.itemsOfVendor.push(`${this.$ln('體彩')}/${searchItems.ball_type.split(',').map(v => this.$ln(this.mapBallType[v])).join(',')}`)
