    mergeTableHeader (headers) {
      const nested = !!headers.some(h => h.children)
      if (nested) {
        let children = []
        const h = headers.map(h => {
          if (h.children && h.children.length > 0) {
            children.push(...h.children)
            return {
              rowspan: 1,
              colspan: h.children.length,
              text: h.text,
              show: h.show,
              align: h.align,
              open: h.open
            }
          }
          return {
            rowspan: 2,
            colspan: 1,
            text: h.text,
            show: h.show,
            align: h.align,
            open: h.open
          }
        })
        return {
          children: children,
          parents: h
        }
      }
      return {
        parents: headers
      }
    },
    // 開關各層級上級退水與未拆帳資訊
    switchCommission (items) {
      for (let p in this.tableList.headers) {
        // headers 各物件如有 stratumId 這 key 做該列顯示與否判斷
        if (this.tableList.headers[p].hasOwnProperty('stratumId')) {
          // 主標題，例如：會員
          this.tableList.headers[p].show = items.indexOf(this.tableList.headers[p].stratumId) !== -1
          // 副標題，例如：返水及結果
          for (let c in this.tableList.headers[p].children) {
            this.tableList.headers[p].children[c].show = items.indexOf(this.tableList.headers[p].stratumId) !== -1
          }
        }
