     * @params  [countItem]    需合計的資料 key 值
     * return  合計總值
     **/
    countTotal (dataList, countItem) {
      let total = 0
      for (let i in dataList) {
        if (dataList[i][countItem]) total += parseFloat(dataList[i][countItem])
      }
      return total
